import React from 'react';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import { setFavorite, deleteFavorite } from '../actions';
import '../styles/characters/character/character.css';

const Character = (props) => {
    const { id, name, Hero, image, sex } = props;
    const handleFavorite = () => {
        props.setFavorite({ id, name, Hero, image, sex });
    }
    const handleDelete = (itemId) => {
        props.deleteFavorite(itemId);
    }
    const sexChararcter = sex;
    const male = sexChararcter==="male" ? "he" : "she";
    const good = Hero ? 'good' : 'bad';
    return (
        <div className="theCharacter">
            <h1>{name}</h1>
            <h2>{male} is {good}</h2>
            <img src={image} alt={name} onClick={handleFavorite} />
            <button type="button" onClick={() => handleDelete(id)}>Delete from Favorites</button>
            <Link to={`/character/${id}`}>See character</Link>
        </div>
    );
}

const mapDispatchToProps =  {
    setFavorite,
    deleteFavorite,
}

export default connect(null, mapDispatchToProps)(Character);