import React from 'react';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import gravatar from '../../utils/gravatar';
import User from './User';

const HeaderList = ({ user }) => {
    return (
        <ul>
            <li><Link to="/login">log in</Link></li>
            <li><Link to="/characters">personages</Link></li>
            <li><img src={Object.keys(user).length > 0 ? gravatar(user.email) : 'data:image/png;base64,' } alt={user.name} /></li>
            <li><User /></li>
        </ul>
    );
}

const mapStateToProps = state => {
    return {
        user: state.user,
    }
}

export default connect(mapStateToProps, null)(HeaderList);