import React from 'react';
import { connect } from 'react-redux';

const User = ({ user }) => {
    return (
        <>
            <p>{user.name ? user.name : 'User'}</p>
        </>
    )
}

const mapStateToProps = state => {
    return {
        user: state.user,
    }
}

export default connect(mapStateToProps, null)(User);