import React, { useState } from 'react';
import { connect } from 'react-redux';
import { requestLogin } from './actions';
import './styles/login/login.css';

const Login = props => {
    const [form, setValues] = useState({
        email: '',
    });

    const handleInput = event => {
        setValues({
            ...form,
            [event.target.name]: event.target.value,
        });
    }

    const handleSubmit = event => {
        event.preventDefault();
        props.requestLogin(form);
        props.history.push('/');
    }

    return (
        <form onSubmit={handleSubmit}>
            <label htmlFor='name'>Name</label>
            <input type='text' name='name' id='name' placeholder='name' autoComplete='name' required onChange={handleInput} />
            <label htmlFor='email'>Email</label>
            <input type='text' name='email' id='email' placeholder='email' autoComplete='email' required onChange={handleInput} />
            <label htmlFor='password'>Password</label>
            <input type='password' name='password' id='password' placeholder='password' autoComplete='password' required onChange={handleInput} />
            <button type='submit'>Send</button>
        </form>
    );
}

const mapDispatchToProps = {
    requestLogin
}

export default connect(null, mapDispatchToProps)(Login);
