const reducer = (state, action) => {
    switch (action.type) {
        case 'SET_FAVORITE':
            const exist = state.favorites.find(item => item.id === action.payload.id)
            if (exist) {
                return {...state}
            }
            else {
                return {
                    ...state,
                    favorites: [...state.favorites, action.payload]
                }
            }
        case 'DELETE_FAVORITE':
            return {
                ...state,
                favorites: state.favorites.filter(items => items.id !== action.payload),
            }
        case 'REQUEST_LOGIN':
            return {
                ...state,
                user: action.payload,
            }
        case 'GET_CHARACTER':
            return {
                ...state,
                character: state.characters.find(item => item.id === Number(action.payload)),
            }
        default:
            return state;
    }
}

export default reducer;